














import { Component, Vue } from 'vue-property-decorator'
import { Chapter1 } from '@/hycal_lib/chapter1'
import { syyType } from '@/hycal_lib/common'

import HycalForm from '@/components/HycalForm/index.vue'

@Component({
  components: {
    HycalForm
  }
})
export default class Cpt1_2 extends Vue {
  private title = '1.2泄槽水面线的计算'
    private initData = {
      实用堰类型: syyType.syy,
      Q_c: 7300,
      b_c: 25,
      n_z: 0.014,
      i: 0.1,
      '\\phi': 0.95,
      H_0: 34.5449,
      N: 5,
      L: 615.34,
      '\\alpha': 1.05
    }
  private data = {
    实用堰类型: '',
    Q_c: '',
    b_c: '',
    n_z: '',
    i: '',
    '\\phi': '',
    H_0: '',
    N: '',
    L: '',
    '\\alpha': ''
  }

  private formulas = {
    0: '$$Q_c :单个泄槽流量,m^3/s$$',
    1: '$$n_z :糙率$$',
    2: '$$b_c :单个泄槽宽,m$$',
    3: '$$i :泄槽底坡$$',
    4: '$$\\phi :流速系数,一般0.95$$',
    5: '$$g :重力加速度,m/s^2,取9.8$$',
    6: '$$\\theta :泄槽底板与水面夹角$$',
    7: '$$L :泄槽长,m$$',
    8: '$$\\varsigma :掺气水深系数,取1～1.4s/m$$',
    9: '$$N :计算断面个数$$',
    10: '$$\\alpha :流速的动力系数$$'
  }

  private demoContent = '某国外（马来西亚巴贡）水电站工程,溢洪道为实用堰,弧形闸门控制,4孔闸,单孔净宽15m,引渠底板高程202m,堰前行近流速为2.6m,PMF洪水下,水库水位233m,堰顶高程209m,下泄流量14600m3/s,泄槽起始底板高程198.80m,2 个泄槽,单个泄槽宽度25m,泄槽长度615.34m,底坡为0.1,计算PMF洪水下泄槽的净水深、掺气水深。'
  private demoResult = '以5个断面为例,求得h1=16.153,h2=11.167,h3=9.438,h4=8.443,h5=7.782'

  get result() {
    let values = Chapter1.slotSurfaceLine(+this.data.实用堰类型, +this.data.Q_c, +this.data.b_c, +this.data.n_z, +this.data.i, +this.data['\\phi'], +this.data.H_0, +this.data.N, +this.data.L, +this.data['\\alpha'])
    let outStrs = ''
    let cnt = 0
    values.forEach(element => {
      cnt++
      outStrs += 'h' + cnt.toString() + '=' + element.toString() + '|'
    })
    return outStrs
  }
}
